/* App container */
.app-container {
    display: flex;
    justify-content: space-around;
  }
  
  /* DataLoadingSection styles */
  .data-loading-section {
    padding: 20px;
    border: 1px solid black;
    width: 250px;
    margin-right: 20px;
  }
  
  .data-loading-section h3 {
    margin: 0;
  }
  
  .data-loading-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .data-loading-section li {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  
  .data-loading-section li .status-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .data-loading-section li .status-text {
    color: black;
    margin-left: 10px;
  }
  
  .data-loading-section li[data-status='error'] .status-text {
    color: red;
  }
  
  /* FilteredColours styles */
  .filtered-colours {
    padding: 20px;
    border: 1px solid black;
    width: 250px;
  }
  
  .filtered-colours h3 {
    margin: 0;
  }
  
  .filtered-colours button {
    margin-bottom: 10px;
  }
  
  .filtered-colours ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .filtered-colours li .color-box {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  /* CirclesDisplay styles */
  .circles-display {
    padding: 20px;
    border: 1px solid black;
    width: 250px;
    margin-bottom: 20px;
  }
  
  .circles-display ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .circles-display li {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  
  .circles-display li .color-box {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
  }
  